body.single-component {
	background: var(--color-neutral-50);

	nav {
		--navigation-top: 80px;
		position: fixed;
		height: calc(100% - var(--navigation-top) - 24px);
		top: var(--navigation-top);
		right: calc(((100vw - var(--main-width)) / 2) + var(--main-width) + 24px);
		padding-right: 24px;
		border-right: 1px solid var(--color-neutral-200);

		span {
			font-weight: 600;
		}

		ul {
			display: flex;
			flex-direction: column;
			gap: 8px;
			padding: 0;
			list-style-type: none;

			a {
				text-decoration: none;
				color: var(--color-neutral-600);
				font-weight: 500;
				transition: all 0.2s ease-in-out;

				&:hover {
					color: var(--color-neutral-900);
				}

				&.active {
					color: var(--color-primary-500);
				}
			}
		}
	}

	main {
		max-width: var(--main-width);
		margin: 80px auto;

		section {
			display: flex;
			flex-direction: column;
			gap: 8px;
			margin-top: 40px;

			h2 {
				font-size: 1.75rem;
				margin: 16px 0;
			}
		}

		#description {
			p {
				margin: 0;
			}

			ul {
				list-style-type: '-';
				padding-inline-start: 24px;
			}
		}

		.actions {
			flex-direction: row;
			gap: 16px;
		}

		.preview {
			position: relative;
			resize: horizontal;
			overflow: hidden;
			max-width: calc(100% - 48px);
			margin-right: auto;
			margin-left: auto;
			padding: 24px;
			border: 1px solid var(--color-neutral-200);
			background: var(--color-neutral-00);

			&__expand {
				position: relative;
				align-self: end;
				margin-bottom: 8px;

				.icon {
					transition: opacity 0.1s ease-in-out;

					&--collapse {
						position: absolute;
						opacity: 0;
					}
				}
			}

			&--expanded {
				position: absolute;
				height: 100dvh;
				width: 100dvw;
				inset: 0;
				z-index: 1;
				margin-top: 0;

				.preview__expand {
					.icon {
						&--expand {
							opacity: 0;
						}
						&--collapse {
							opacity: 1;
						}
					}
				}
			}
		}

		.tabs#code {
			overflow: hidden;
			border: 1px solid var(--color-neutral-700);
			color-scheme: dark;

			.tabs-list {
				position: relative;
				display: flex;
				gap: 16px;
				padding-inline: 16px;
				border-bottom: 1px solid var(--color-neutral-700);
				background: var(--color-neutral-800);

				.tabs-trigger {
					height: 48px;
					padding-inline: 16px;
					font-weight: 500;
					font-size: 0.875rem;
					color: var(--color-neutral-300);
					border: none;
					background: none;

					&:nth-child(1):not([aria-selected]),
					&[aria-selected='true'] {
						color: var(--color-neutral-100);
					}
				}

				.tabs-indicator {
					position: absolute;
					bottom: -1px;
					left: 0;
					height: 1px;
					background: var(--color-primary-500);
					transition: left 0.2s ease-in-out, width 0.2s ease-in-out;
				}
			}

			.tabs-panel {
				position: relative;
				max-height: 560px;
				overflow: auto;
				scrollbar-width: thin;

				background: var(--color-neutral-900);
				padding: 16px;
				color: var(--color-neutral-300);
				font: 400 0.875rem monospace;

				.copy {
					position: absolute;
					top: 16px;
					right: 16px;
					padding: 8px;
					border: 0;
					background: none;
					color: var(--color-neutral-300);

					.icon {
						display: block;
						transition: opacity 0.1s ease-in-out;

						&--check {
							position: absolute;
							inset: 8px;
							opacity: 0;
						}
					}

					&:hover {
						background: var(--color-neutral-800);
					}

					&--success {
						.icon {
							&--copy {
								opacity: 0;
							}

							&--check {
								opacity: 1;
							}
						}
					}
				}

				.code {
					// background: var(--color-neutral-900);
					// padding: 16px;
					// color: var(--color-neutral-300);
					// font: 400 0.875rem monospace;

					p {
						margin: 0;
					}

					pre {
						margin: 0;
					}
				}
			}
		}
	}

	&:has(#preview.preview--expanded) {
		overflow: hidden;
	}

	&.admin-bar nav {
		--navigation-top: calc(80px + var(--wp-admin--admin-bar--height));
	}
}
